@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap');

body {
    font-family: "Roboto", sans-serif;
    background-color: #1c1c1c;
    color: #fff;
    margin: 0; }
a {
    color: #fff;
    text-decoration: none; }

ul {
    list-style: none;
    margin: 0;
    padding: 0; }

h1, h2, h3, h4, h5, h6 {
    font-weight: 500;
    margin: 0; }
img {
    max-width: 100%; }
.container {
    max-width: 1170px;
    margin: 0 auto;
    padding: 0 15px;
    box-sizing: border-box; }
.btn {
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 500;
    font-size: 16px;
    width: 165px;
    height: 50px;
    background: #F6762C;
    border-radius: 5px;
    transition: opacity 0.3s;
    &:hover {
        opacity: 0.8; } }



.header {
    padding: 25px 0;
    &__container {
        display: flex;
        justify-content: space-between;
        align-items: center; }
    &__nav {
        a {
            font-size: 14px;
            opacity: 0.7;
            transition: 0.3s;
            &:hover {
                opacity: 1; } }
        a.active {
            opacity: 1;
            &:hover {
                opacity: 0.7; } } }

    &__list {
        display: flex;
        gap: 30px; } }
.welcome {
    box-sizing: border-box;
    min-height: 100vh;
    padding: 240px 0;
    margin-top: -100px;
    background-image: url("../img/background-burger.png");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center; }
.welcome__container {
    &__wrapper {
        width: 550px; }
    &__subtitle {
        margin-bottom: 7px;
        font-size: 14px;
        letter-spacing: 0.1em; }
    &__title {
        margin-bottom: 37px;
        font-size: 58px; }
    &__desc {
        margin-bottom: 42px;
        font-size: 18px;
        opacity: 0.7; } }

.food-list {
    padding: 100px 0;
    .container {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        gap: 10px; }
    &__card {
        overflow: hidden;
        text-align: center;
        width: 260px;
        background: #312F30;
        border-radius: 10px;
        &__title {
            font-size: 20px;
            margin-bottom: 13px; }
        &__desc {
            font-size: 16px;
            padding-bottom: 30px;
            opacity: 0.7; }
        img {
            width: 100%;
            height: 175px;
            object-fit: cover;
            margin-bottom: 28px; } } }

.order {
    background: #312F30;
    padding: 60px 0;
    .btn {
        margin-top: 10px; }
    &__title {
        font-size: 44px;
        margin-bottom: 25px; }
    &__desc {
        font-size: 16px;
        opacity: 0.7;
        margin-bottom: 15px; }
    &__price {
        font-weight: 500;
        font-size: 20px;
        letter-spacing: 0.1em;
        color: #F6762C; }
    &__old-price {
        font-weight: 500;
        font-size: 16px;
        letter-spacing: 0.1em;
        color: #F6762C;
        opacity: 0.5;
        text-decoration: line-through; }
    .container {
        display: flex;
        padding-right: 90px;
        justify-content: space-between;
        align-items: center; } }


.product {
    padding: 150px 0;
    .container {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        gap: 30px; }

    &__card {
        box-sizing: border-box;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        width: 555px;
        mim-height: 300px;
        padding: 60px 0 60px 290px;
        &:nth-child(even) {
            padding: 60px 0 60px 50px; }
        &__title {
            font-size: 30px;
            margin-bottom: 20px; }
        &__desc {
            font-size: 16px;
            opacity: 0.7;
            margin-bottom: 10px; }
        &__price {
            font-weight: 500;
            font-size: 16px;
            letter-spacing: 0.1em;
            color: #F6762C; }
        &__old-price {
            font-weight: 500;
            font-size: 16px;
            letter-spacing: 0.1em;
            opacity: 0.5;
            color: #F6762C;
            text-decoration: line-through; }
        .btn_small {
            font-size: 12px;
            width: 140px;
            height: 40px; }
        .btn {
            margin-top: 10px; } } }

.card-queen {
    background-image: url("../img/queen.png"); }
.card-hut {
    background-image: url("../img/hut.png"); }
.card-king {
    background-image: url("../img/king.png"); }
.card-papa {
    background-image: url("../img/papa.png"); }

